module.exports = () => (
  window.onload = () => {
    if($('#js-styleguide').length) {
      $('body').scrollspy({ target: '#js-styleguide' });
    }
    if($('#js-rlt-guide').length) {
      $('body').scrollspy({ target: '#js-rlt-guide' });
    }
    if($('#js-styleguidist').length) {
      $('body').scrollspy({ target: '#js-styleguidist' });
    }
    if($('#js-stylelint').length) {
      $('body').scrollspy({ target: '#js-stylelint' });
    }
  }
);
